.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.customSwitch {
  vertical-align: middle;
    margin: 10px;
}

.white {
    color:white;
}

.bookmark, .bookmarkDetailed {
    margin: 10px;
    color: white;
    cursor: pointer;
}

.bookmarkDetailed {
    margin-left: 50px; display: inline;
}

.foo-bar {
    color: #090a01 !important;
}

.SharingToastContainer{

}

.SharingToast{
    color: #090a01 !important;
    font-size: 16px;
    font-weight: 700;
}

.paddedButtonDiv > button{
    padding-left: 20px !important;
    padding-right: 20px !important;
}

.chiclets{
    height: 2em;
}

.articleCard {
    /*height: 180px;*/
    height: auto;
    box-shadow: -1px 7px 22px #80808085;
    cursor: pointer;
}
.articleCardDetailed {
    box-shadow: -1px 7px 22px #80808085;
    transition: height 0.2s ease-out;
}

.articleCardImage {
    height: 100%;
}

.articleCardImage > * {
    background-size: cover;
    width: 100%;
    box-shadow: 0px 0px 5px #80808085;
    height: 100%;
    border: 3px solid white;
    border-radius: 6px;
}

.articleCardDetailedImage > * {
    width: 100%;
    background-size: cover;
}



.articleSmallContent{
    height: 100%;
    overflow: hidden;
    font-size: 14px;
}

.articleSummary {
    height: 50%;
    overflow: hidden;

}


.articleSummaryDetailed {

    overflow: hidden;
    white-space: pre-wrap;

    transition: max-height 0.5s ease-out;
    height:auto;

    max-height: 100px;

}

.shareDetailed {
    height: auto;
    width: 200px;
    margin-left: auto;
    float: right;
    display: block;
}

/*.shareDetailed > button {*/
    /*padding: 10px!important;*/
/*}*/


.expanded-false {
    max-height: 100px;
}

.expanded-true {
    max-height: 10000px !important;
}

.dropdown {
    height: 50px;
    width: 50px;
    display: block;
    margin-left: auto;
    float: right;

}

.articleTitle {
    font-style: italic;
    font-weight: bold;
    font-size: 14px;
}

.articleTitleText{
    display: inline;
}

.articleDate {
    font-style: italic;
    float: left;
}

.articleTitleDetailed {
    font-style: italic;
    font-size: 28px;
}

.articleDateDetailed {
    font-style: italic;
    float: left;
    font-size: 18px;
}

.newsLabel{
    line-height: 3em;
}

.noArticles{
    width: 50%;
    margin: auto;
    text-align: center;
}

.articleType {
    float: right;
}



.commentbox {
    width: 100%;
}



.loadingMessage {
    text-align: center;
    width: 100%;
    font-size: 22px;
    font-weight: bolder;
    margin: 50px auto;
}

.badge-world {
    color: #fff;
    background-color: #7045ff;
}

.badge-politics {
    color: #fff;
    background-color: #39897d;
}

.badge-business {
    color: #fff;
    background-color: #3c8ae8;
}

.badge-technology {
    color: #090a01;
    background-color: #c8d732;
}

.badge-sports, .badge-sport {
    color: #090a01;
    background-color: #f5ba3c;
}

.badge-health {
    color: #fff;
    background-color: #636a71;
}

.badge-gFav {
    color: #fff;
    background-color: #142441;
}

.badge-nFav {
    color: #050505;
    background-color: #d8d8d8;
}








.bg-grad {
    background: rgb(23,34,65);
  background: -moz-linear-gradient(90deg, rgba(23,34,65,1) 0%, rgba(63,93,176,1) 100%);
  background: -webkit-linear-gradient(90deg, rgba(23,34,65,1) 0%, rgba(63,93,176,1) 100%);
  background: linear-gradient(90deg, rgba(23,34,65,1) 0%, rgba(63,93,176,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#172241",endColorstr="#3f5db0",GradientType=1);
}

label {

  margin-bottom: 0.0rem !important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
